import React, { Component } from 'react'
import MyNavigation from './MyNavigation'
import Crossword from './CrosswordComponentsV2/Crossword'
import CrestGAN from './ArticleComponents/CrestGAN'
import TwitterFC from './ArticleComponents/TwitterFC'
import {Container, CssBaseline} from '@material-ui/core'

export default class Portfolio extends Component {
    constructor(props){
        super(props);
        this.state = {project:'crossword'}
    }

    handleProjectDropdownChange = (e, popupState) => {
        popupState.close()
        this.setState({project:e.target.id})
    }
    render() {
        const articleStyle = {
            // top right bottom left 
            padding: "7% 5% 0px 20%"
          };
        var project;
        switch(this.state.project){
            case 'gan':
                project = 
                <> 
                    <h1> Full Report Coming Soon! </h1> <CrestGAN/> 
                </> 
                break;
            case 'crossword':
                project = 
                <> 
                    <h2>Crossword Interface</h2>
                    <p> This is a Crossword Interface I built in React showcasing the first Crossword that I wrote myself. Go ahead and try to solve it! </p> 
                    <p> Press <b>Enter</b> to change direction and use the arrow keys to move around.
                    Press <b>Shift</b> to advance to the next clue or click a clue or square to move to it.</p>
                    <p>Still tons of room for improvement and I'm working to make the interface better every day.</p> 
                    <Crossword/>  
                </>
                break;
            case 'twfc':
                project = <TwitterFC/>
                break;
            default:
                project = <h1>Should not be seeing this</h1>
        }


        return (
            <>
                <CssBaseline/> 
                <MyNavigation article={this.state.project} handleProjectDropdownChange={this.handleProjectDropdownChange}/> 
                <Container style={articleStyle}>            
                    {project}
                </Container>
            </>
        )
    }
}
