import React, { Component } from "react";
import CrosswordJSON from "../../assets/MyCrossword";
import Square from "./Square"
import ClueList from "./ClueList"
import "./Crossword.css";
import {Button} from "@material-ui/core"

export default class Crossword extends Component {
  // React Methods
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.solution = CrosswordJSON.Grid;
    this.clues = CrosswordJSON.Clues;

    this.gridSize = CrosswordJSON.Size[0];
    this.squareSize = "40px ";

    this.state = {
      grid: this.initializeUserGridFromSolution(this.solution),
      userPosition: this.initilaizeUserPosition(this.solution),
      isAcross: true,
      currentClue:1,
      isInCorrectMode:false
    };
    this.boardStyle = {
        gridTemplateColumns: this.squareSize.repeat(this.gridSize),
        gridTemplateRows: this.squareSize.repeat(this.gridSize)
    };
    this.clueInfoArray = this.initializeClueInfoArray(this.state.grid);
  }
  componentDidMount() {
    window.addEventListener("keydown", this.handleKeyDown);
    // this.myRef.current.focus();
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.handleKeyDown);
  }
  toggleAcross = () => {
      this.setState({isAcross:!this.state.isAcross})
  }
  // Initialization Methods
  // ex: Index the grid with proper clue numbers at the start 
  initializeClueInfoArray = grid => {
    let counter = 1;
    var clueInfo = [];
    for (let i = 0; i < grid.length; i++) {
      if (grid[i] == null) {
        clueInfo.push(0);
      } else {
          const isDown = grid[i - this.gridSize] == null
          const isAcross = i % this.gridSize === 0 || grid[i - 1] == null
          if (isDown && isAcross) {
            clueInfo.push([counter++,"DA"])  
        } else if (isDown) {
            clueInfo.push([counter++,"D"])  
        } else if (isAcross){
            clueInfo.push([counter++,"A"])              
        } else {
          clueInfo.push(0);
        }
      }
    }
    return clueInfo;
  };

  initializeUserGridFromSolution = solution => {
    return solution.map(letter => (letter !== "" ? "" : null));
  };

  initilaizeUserPosition = grid => {
      return grid.findIndex(element => element !== "");
  };

  // Methods for handling Events/User Inputs
  // ex: Key Pressing or Clicking on Square
  updateGrid = (key, index) => {
    let newGrid = this.state.grid;
    newGrid[index] = key.toUpperCase();
    this.setState({ grid: newGrid });
  };
  handleKeyDown = e => {
    const { key } = e;
    switch (key) {
      case "ArrowLeft":
        if (!this.state.isAcross){
            this.toggleAcross()
            this.setState({currentClue:this.findClueNumber(this.state.isAcross,this.state.userPosition,this.clueInfoArray)})
        } else {
        this.moveLeft();
        }
        break;
      case "ArrowRight":
        if (!this.state.isAcross){
            this.toggleAcross()
            this.setState({currentClue:this.findClueNumber(this.state.isAcross,this.state.userPosition,this.clueInfoArray)})
        } else {
            this.moveRight();
        }
        break;
      case "ArrowUp":
        if (this.state.isAcross){
            this.toggleAcross()
            this.setState({currentClue:this.findClueNumber(this.state.isAcross,this.state.userPosition,this.clueInfoArray)})
        } else {
            this.moveUp();
        }
        break;
      case "ArrowDown":
        if (this.state.isAcross){
            this.toggleAcross()
            this.setState({currentClue:this.findClueNumber(this.state.isAcross,this.state.userPosition,this.clueInfoArray)})              
        } else {
            this.moveDown();
        }
        break;
      case "Enter":
        this.toggleAcross();
        this.setState({currentClue:this.findClueNumber(this.state.isAcross,this.state.userPosition,this.clueInfoArray)})
        break;
      case "Shift":
        this.goToNextClue();
        break;
      case "Backspace":
        this.updateGrid("", this.state.userPosition);
        this.state.isAcross ? this.moveLeft() : this.moveUp();
        break;
      case "Meta":
      case "Tab":
        break;
      default:
        this.updateGrid(key, this.state.userPosition);
        this.state.isAcross ? this.moveRight() : this.moveDown();
    }
  };

  moveLeft = () => {
    if (this.state.userPosition % this.gridSize !== 0) {
      let newPosition = this.state.userPosition - 1;
      if (this.state.grid[newPosition] != null) {
        this.setState({ userPosition: newPosition });
      }
    }
  };
  moveRight = () => {
    if (this.state.userPosition % this.gridSize !== this.gridSize - 1) {
      let newPosition = this.state.userPosition + 1;
      if (this.state.grid[newPosition] != null) {
        this.setState({ userPosition: newPosition });
      } else {
        this.goToNextClue();
        }
    } else {
        this.goToNextClue();
    }
  };
  moveUp = () => {
    let newPosition = this.state.userPosition - this.gridSize;
    if (this.state.grid[newPosition] != null) {
      this.setState({ userPosition: newPosition });
    }
  };
  moveDown = () => {
    let newPosition = this.state.userPosition + this.gridSize;
    if (this.state.grid[newPosition] != null) {
      this.setState({ userPosition: newPosition });
    } else {
        this.goToNextClue();
    }
  };

  handleClueClick = (e) => {
    const newLocation = this.clueInfoArray.findIndex(clue => clue[0] === Number(e.target.id))
    const newDirection = this.clueInfoArray[newLocation][1] === "A" 
    this.setState({
        userPosition:newLocation,
        isAcross:newDirection,
        currentClue:this.clueInfoArray[newLocation][0]
    });
  };

  handleCheckWord = () => {
    alert("Check Word not implemented yet");
    this.myRef.current.focus();
  };

  handleCheckPuzzle = () => {
    this.setState({isInCorrectMode:!this.state.isInCorrectMode})
    this.myRef.current.focus();
  };

  handleClear = () => {
    this.setState({ grid: this.initializeUserGridFromSolution(this.solution) });
    this.myRef.current.focus();
  };
  
  handleMousePress = (e) => {
      const indexClicked = Number(e.target.id)
      if (indexClicked === this.state.userPosition){
        this.toggleAcross()
        this.setState({
            currentClue:this.findClueNumber(!this.state.isAcross,indexClicked,this.clueInfoArray)
        })
      } else {
        this.setState({
            userPosition:indexClicked,
            currentClue:this.findClueNumber(this.state.isAcross,indexClicked,this.clueInfoArray)
        })
      }
  }

  goToNextClue = () => {
      const newClueIndex = this.clueInfoArray.findIndex((clue) => {
          if (!clue){
              return false;
          } else {
              if (this.state.isAcross){
                return(clue[0] > this.state.currentClue && clue[1].includes("A"))
              } else {
                return(clue[0] > this.state.currentClue && clue[1].includes("D"))
              }
          }
      })
      if (newClueIndex !== -1){
        this.setState({
          userPosition: newClueIndex,
          currentClue: this.clueInfoArray[newClueIndex][0]
        })
      } else {
          const firstOfOppositeIndex = this.clueInfoArray.findIndex((clue => {
              return clue[0] === 1;
          }))
          this.setState({
              userPosition:firstOfOppositeIndex,
              currentClue:1
          })
          this.toggleAcross();
      }
  }
  findClueNumber = (isAcross,position,clueInfoArray) => {
    for(let i = 0; i < clueInfoArray.length; i++){
        if (isAcross){
            if (clueInfoArray[i][1]){
                if (clueInfoArray[i][1].includes("A")){
                    if (this.areHorizontallyAligned(i,position) || i === position){
                        return clueInfoArray[i][0]
                    }
                }
            }
        } else {
            if (clueInfoArray[i][1]){
                if (clueInfoArray[i][1].includes("D")){
                    if (this.areVerticallyAligned(i,position) || i === position){
                        return clueInfoArray[i][0]               
                    }
                }
            }
        }
    }
  }

areVerticallyAligned = (x, y) => {
    if (y > x) {
    [x, y] = [y, x];
    }
    while (x > 0) {
    x -= this.gridSize;
    if (x === y) {
        return true;
    }
    if (this.state.grid[x] === null) {
        break;
    }
    }
};

areHorizontallyAligned = (x, y) => {
    let distanceFromLeft = y % this.gridSize;
    let distanceFromRight = this.gridSize - distanceFromLeft;
    if (x >= y - distanceFromLeft && x < y + distanceFromRight) {
      if (y > x) {
        [x, y] = [y, x];
      }
      while (x > 0) {
        x -= 1;
        if (x === y) {
          return true;
        }
        if (this.state.grid[x] === null) {
          break;
        }
      }
    }
  };
  renderGrid = (grid) => {
    return grid.map((letter, index) => (
      <Square
        letter={letter}
        index={index}
        userPosition={this.state.userPosition}
        handlePositionChangeClick={this.handleMousePress}
        clueInfoArray={this.clueInfoArray}
        isAcross={this.state.isAcross}
        key={index}
        areHorizontallyAligned={this.areHorizontallyAligned}
        areVerticallyAligned={this.areVerticallyAligned}
        isWrong={this.solution[index] !== this.state.grid[index]}
        isInCorrectMode={this.state.isInCorrectMode}
      />
    ));
  };

  render() {
    return (
      <form className="crossword" tabIndex="-1" ref={this.myRef}>
        <div className="actions-menu">
          <Button variant="outlined" onClick={this.handleCheckWord}>
            Check Word
          </Button>
          <Button variant="outlined" onClick={this.handleCheckPuzzle}>
            Check Puzzle
          </Button>
          <Button variant="outlined" onClick={this.handleClear}>
            Clear
          </Button>
          <p className="autocheck">Autocheck: {this.state.isInCorrectMode ? "On" : "Off"}</p>
        </div>
        <br />
        <div className="board-and-clues">
          <div>
            <div className="clue-highlighted">
                <div className="grid-clue-number">{this.currentClue}</div>
                <div>{this.clueInfoArray[this.currentClue]}</div> 
            </div>
            <div className="board" style={this.boardStyle}>
                {this.renderGrid(this.state.grid)}
            </div>
          </div>
          <ClueList
            title="Across"
            clues={this.clues.Across}
            currentClue={this.state.currentClue}
            handleClueClick={this.handleClueClick}
            isAcross={this.state.isAcross}
            isAcrossClues={true}
          />
          <ClueList
            title="Down"
            clues={this.clues.Down}
            currentClue={this.state.currentClue}
            handleClueClick={this.handleClueClick}
            isAcross={this.state.isAcross}
            isAcrossClues={false}
          />
        </div>
      </form>
    );
  }
}
